/* COLORS */
:root {
  --mycolor-accent-light: #eaf4ff;
  --mycolor-accent: #dceaff;
  --mycolor-accent-dark: #aecefd;
  --mycolor-accent-highlight: #0d6efd;

  /*  */
  --sidebar-width: 250px;
}

/* SCROLL BAR STYLE */
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0);
  border-radius: 10px;
  /* background-color: #F5F5F5; */
}

::-webkit-scrollbar {
  width: 8px;
  /* background-color: #F5F5F5; */
}

::-webkit-scrollbar-thumb {
  /* border-radius: 10px; */
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #555;
}
